@import '../variables';

.categories {
  ul {
    display: flex;
    flex-wrap: wrap;
    row-gap: 10px;
    margin-right: 20px;

    li {
      background-color: #f9f9f9;
      padding: 13px 30px;
      border-radius: 30px;
      margin-right: 10px;
      font-weight: bold;
      cursor: pointer;
      transition: background-color 0.1s ease-in-out;
      @include noselect();

      &:hover {
        background-color: darken(#f9f9f9, 2%);
      }

      &:active {
        background-color: darken(#f9f9f9, 5%);
      }

      &.active {
        background-color: #282828;
        color: #fff;
      }
    }
  }

  @media (max-width: 768px) {
    ul {
      margin-right: 0;
      justify-content: space-between;
    }
  }

  @media (max-width: 580px) {
    width: 100%;

    ul {
      width: 100%;
      flex-direction: column;

      li {
        margin-right: 0;
        width: 100%;
        text-align: center;
      }
    }
  }
}
